import { useState ,useEffect} from "react";
import Introduction from "./Introduction";
import SkillList from "./SkillList";

const Home = () => {
    // let name = 'Nazanin';
    const [skills, setskills] = useState(null);
    const [isPending, setIsPending] = useState(true);
    const [error, setError] = useState(null);
    useEffect(() =>{
        fetch('http://localhost:8000/skills')
            .then(response =>{
                if(!response.ok){
                    throw Error('Could not fetch data');
                }
            return response.json()
            })
            .then(data => {
                setskills(data);
                setIsPending(false);
                setError(null);
            })
            .catch(err =>{
                setError(err.message);
                setIsPending(false);
            })
    },[]);
    return (  
        <div className="home">
         {error && <div>{error}</div>}
         {isPending && <div>Loading ...</div>}
         <Introduction/>
         {skills && <div className="skill-sect"><SkillList skills={skills.filter((skill)=>skill.class==='Web Development')} title="My Web Skills"/> </div>}
         {skills && <div className="skill-sect"><SkillList skills={skills.filter((skill)=>skill.class==='AI')} title="My AI Skills"/></div>}
         
         </div>
    );
}
 
export default Home;